* {
  padding: 0;
  margin : 0;
  border : 0;
}

*,
*::before,
*:after {
  -moz-box-sizing   : border-box;
  -webkit-box-sizing: border-box;
  box-sizing        : border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height                  : 100%;
  width                   : 100%;
  font-size               : 100%;
  line-height             : 1;
  font-size               : 16px;
  -ms-text-size-adjust    : 100%;
  -moz-text-size-adjust   : 100%;
  -webkit-text-size-adjust: 100%;
  font-family             : 'Raleway', sans-serif;
  font-weight             : 400;
  color                   : #2F2F2F;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button ::-moz-focus-inner {
  padding: 0;
  border : 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size  : inherit;
  font-weight: 400;
  color      : #2F2F2F;
}

.container {
  max-width: 1630px;
  padding  : 0 15px;
  margin   : 0 auto;
}

#root {
  display        : flex;
  flex-direction : column;
  justify-content: space-between;
  min-height     : 100vh;
}